
const generateOutput = (text: string, output = '0') => {
    switch (text) {
        case '':
            if (output.length === 1) {
                return '0'
            } else {
                return output.slice(0, -1) || ''
            }
        case '':
            return ('');
        case '.':
            if (output.indexOf('.') >= 0) { return output }
            else {
                return output + '.'
            }
        default:
            if (output === '0') {
                return text
            } else {
                return output + text
            }
    }
}

export { generateOutput }